.Input {
   margin-bottom: 15px;  
}

.Input label {
   margin-bottom: 3px;
   padding: 0;
   display: block;
   font-weight: 900;
}

.Input input {
   display: block;
   box-sizing: border-box;
   border: 1px solid #bebebe;
   padding: 7px;
   margin: 0 0 5px;
   width: 100%;
   outline: none;
   transition: all .3s ease-in-out;
}

.Input span {
   color: darkred;
   font-size: 12px;
   font-weight: 900;
}

.Input.invalid label {
   color: darkred;
}